body {
  --color-blue: #00acdc;
  margin: 0;
}

.header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.logoImg {
  width: 100%;
  height: 100%;
}

.menu {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.menuItem {
  margin-left: 20px;
}

.menuItem:first-child {
  margin-left: 0;
}

.menuLink {
  white-space: nowrap;
  text-transform: uppercase;
  color: #000;
  align-items: center;
  height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
  display: flex;
  position: relative;
}

.menuLink:hover, .menuLink.is-active {
  color: var(--color-blue);
}

.menuLink.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
